section {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
section h2 {
  font-size: 10em;
}
.triggered {
  position: fixed;
  top: 0;
  bottom: 0;
  margin: auto;
}
section:nth-child(2) {
  background-color: #eda664;
}
section:nth-child(3) {
  background-color: #b6f8a1;
}
section:nth-child(4) {
  background-color: #a1c7f8;
}
section:nth-child(5) {
  background-color: #f8a1cf;
}
